 * {
    line-height: 20px;
    user-select:none;
    font-family: 'Inter', sans-serif;
    overflow: hidden;
}

@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300&display=swap');

.main {
    width: 28%;
    display: flex;
    flex-direction: column;
}

.main--container-array {
    min-height: 250px;
    display: flex;
    flex-direction: column;
    padding: 18px;
    width: 50%;
}

.main--container-array:nth-child(1) {
    background-color: #84C783;
    border-bottom-right-radius: 30px;
    border-top-right-radius: 30px;
    color: white;
}

.main--container--arrays {
    background-color: #cedbce;
    border-bottom-right-radius: 30px;
    width: 100%;
}

.clickable-span {
    cursor: pointer;
    min-height: 20px;
    margin: 4px;
    padding: 2px 0 2px 0;
}

.main--container-header {
    display: flex;
    justify-content: space-between;
    background-color: #447843;
    align-items: center;
    height: 50px;

}

.main--container-header span {
    color: white;
    flex: 1;
    font-weight: bold;
}

.main--container--arrays {
    display: flex;
}

.main--container--buttons {
    cursor: pointer;
    width: 28px;
    margin-bottom: 8px;
}

.arrow-right {
    align-self: center;
    transition: transform 0.2s ease-in-out;
}

.arrow-right:hover {
    transform: translate(6px);
}

.arrow-left {
    align-self: center;
    transition: transform 0.2s ease-in-out;
}

.arrow-left:hover {
    transform: translate(-6px);
}